"use client";

import { useState } from "react";
import { useForm } from "@tanstack/react-form";
import { Button } from "@trading-platform/ui/button";
import { Input } from "@trading-platform/ui/input";

interface WaitlistFormB2CProps {
  source?: string;
  className?: string;
  placeholder?: string;
  buttonText?: string;
  variant?: "inline" | "stacked";
}

export function WaitlistFormB2C({
  source = "landing_page_b2c",
  className,
  placeholder = "Enter your email",
  buttonText = "Join Waitlist",
  variant = "inline",
}: WaitlistFormB2CProps) {
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const form = useForm({
    defaultValues: {
      email: "",
    },
    onSubmit: async ({ value }) => {
      setLoading(true);
      setError(null);

      try {
        const response = await fetch("/api/waitlist", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            email: value.email,
            role: "customer",
            source,
          }),
        });

        const data = await response.json();

        if (!response.ok) {
          throw new Error(data.error || "Failed to join waitlist");
        }

        setSuccess(true);
      } catch (err) {
        setError(err instanceof Error ? err.message : "Something went wrong");
      } finally {
        setLoading(false);
      }
    },
  });

  if (success) {
    return (
      <div className={className}>
        <div className="rounded-lg border border-green-500 bg-green-50 p-4 text-center dark:bg-green-950">
          <div className="mb-2 text-3xl">ðŸŽ‰</div>
          <h3 className="mb-1 text-lg font-bold text-green-900 dark:text-green-100">
            You&apos;re in!
          </h3>
          <p className="text-sm text-green-800 dark:text-green-200">
            Check your inbox for next steps.
          </p>
          <p className="mt-2 text-xs text-green-700 dark:text-green-300">
            Early members get instant Silver status (500 points free)
          </p>
        </div>
      </div>
    );
  }

  return (
    <div className={className}>
      <form
        onSubmit={(e) => {
          e.preventDefault();
          e.stopPropagation();
          form.handleSubmit();
        }}
        className={
          variant === "inline"
            ? "flex flex-col gap-2 sm:flex-row"
            : "space-y-3"
        }
      >
        <form.Field
          name="email"
          validators={{
            onChange: ({ value }) => {
              if (!value) return "Email is required";
              if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(value))
                return "Invalid email";
              return undefined;
            },
          }}
        >
          {(field) => (
            <div className="flex-1">
              <Input
                type="email"
                placeholder={placeholder}
                value={field.state.value}
                onChange={(e) => field.handleChange(e.target.value)}
                onBlur={field.handleBlur}
                disabled={loading}
                className={variant === "inline" ? "h-12" : "h-11"}
                required
              />
              {field.state.meta.errors && (
                <p className="mt-1 text-sm text-red-500">
                  {field.state.meta.errors[0]}
                </p>
              )}
            </div>
          )}
        </form.Field>

        <Button
          type="submit"
          className={variant === "inline" ? "h-12 px-8" : "w-full"}
          disabled={loading}
        >
          {loading ? "Joining..." : buttonText}
        </Button>
      </form>

      {error && (
        <div className="mt-2 rounded-md border border-red-300 bg-red-50 p-2 text-sm text-red-800 dark:bg-red-950 dark:text-red-200">
          {error}
        </div>
      )}

      {variant === "stacked" && (
        <p className="mt-2 text-center text-xs text-gray-500">
          Free to join. No credit card required.
        </p>
      )}
    </div>
  );
}
